@import url(https://fontlibrary.org/face/glacial-indifference);
@import url(https://use.fontawesome.com/releases/v5.5.0/css/all.css);
:root {
  --primary: #2980b9;
  --secondary-color-1: #34495e;
  --secondary-color-2: #2c3e50;
  --secondary-color-3: #212b36;
}

html,
body {
  font-family: "GlacialIndifferenceRegular", sans-serif;
  padding: 0;
  margin: 0;
  color: white;
  overflow: auto;
  background: #34495e !important;
  background: var(--secondary-color-1) !important;
}

a {
  -webkit-user-drag: none;
}

:not(input):not(textarea):not(button):not(span):not(div):not(a):not(i):not(span),
:not(input):not(textarea):not(button):not(span):not(div):not(a):not(i):not(span)::after,
:not(input):not(textarea):not(button):not(span):not(div):not(a):not(i):not(span)::before {
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  cursor: default;
}

img {
  -webkit-user-drag: none;
}

input,
button,
textarea,
:focus {
  outline: none;
}

a:hover,
a:focus {
  text-decoration: none !important;
}

::-webkit-scrollbar {
  background-color: #212b36;
  background-color: var(--secondary-color-3);
  width: 8px;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 8px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #212b36;
  box-shadow: inset 0 0 6px var(--secondary-color-3);
  background-color: #212b36;
  background-color: var(--secondary-color-3);
  border-radius: 8px;
}

.ant-btn-primary {
  background-color: #2980b9 !important;
  background-color: var(--primary) !important;
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-btn-ghost {
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-btn-group .ant-btn-primary:first-child:not(:last-child) {
  border-right-color: #2980b9;
  border-right-color: var(--primary);
}

.ant-btn-group .ant-btn-primary:last-child:not(:first-child),
.ant-btn-group .ant-btn-primary + .ant-btn-primary {
  border-left-color: #2980b9;
  border-left-color: var(--primary);
}

.ant-btn-ghost:hover,
.ant-btn-ghost:focus,
.ant-btn-ghost:active,
.ant-btn-ghost.active {
  color: #2980b9;
  color: var(--primary);
  border-color: #2980b9;
  border-color: var(--primary);
}

.ant-btn-primary:hover,
.ant-btn-primary:focus {
  background-color: #2980b9 !important;
  background-color: var(--primary) !important;
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
  -webkit-filter: brightness(85%) !important;
          filter: brightness(85%) !important;
}

.ant-cascader-picker {
  background-color: #2c3e50 !important;
  background-color: var(--secondary-color-2) !important;
}

.ant-cascader-picker-label:hover ~ .ant-input {
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
}

.ant-cascader-menus {
  background-color: #2c3e50 !important;
  background-color: var(--secondary-color-2) !important;
}

.ant-cascader-menu-item:hover {
  background-color: #212b36 !important;
  background-color: var(--secondary-color-3) !important;
}

.ant-cascader-menu-item-active:not(.ant-cascader-menu-item-disabled),
.ant-cascader-menu-item-active:not(.ant-cascader-menu-item-disabled):hover {
  background-color: #212b36 !important;
  background-color: var(--secondary-color-3) !important;
}

.ant-btn-primary.disabled,
.ant-btn-primary[disabled],
.ant-btn-primary.disabled:hover,
.ant-btn-primary[disabled]:hover,
.ant-btn-primary.disabled:focus,
.ant-btn-primary[disabled]:focus,
.ant-btn-primary.disabled:active,
.ant-btn-primary[disabled]:active,
.ant-btn-primary.disabled.active,
.ant-btn-primary[disabled].active {
  background-color: #2980b9 !important;
  background-color: var(--primary) !important;
  color: white !important;
  -webkit-filter: brightness(60%) grayscale(30%) !important;
          filter: brightness(60%) grayscale(30%) !important;
}

.ant-switch-checked,
.ant-checkbox-checked .ant-checkbox-inner,
.ant-checkbox-indeterminate .ant-checkbox-inner {
  background-color: #2980b9 !important;
  background-color: var(--primary) !important;
}

.ant-switch-loading-icon,
.ant-switch:after {
  background-color: #fff !important;
}
.ant-input,
.ant-input-group-addon {
  background-color: #2c3e50 !important;
  background-color: var(--secondary-color-2) !important;
  border-color: #2c3e50 !important;
  border-color: var(--secondary-color-2) !important;
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled) {
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-input:focus,
.ant-cascader-picker:focus .ant-cascader-input {
  box-shadow: none !important;
}

.ant-input-group-addon:hover,
.ant-input-group-addon:focus,
.ant-input-group-addon:active,
.ant-input-group-addon svg,
.ant-input-group-addon svg path {
  background-color: #34495e !important;
  background-color: var(--secondary-color-1) !important;
  cursor: pointer !important;
}

.ant-input-affix-wrapper .ant-input-prefix :not(.anticon),
.ant-input-affix-wrapper .ant-input-suffix :not(.anticon),
.ant-select-arrow .ant-select-arrow-icon svg,
.ant-select-arrow .ant-select-arrow-icon svg path {
  cursor: pointer !important;
}

.ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled),
.ant-input:focus,
.ant-input:hover,
.ant-cascader-picker:focus .ant-cascader-input,
.ant-checkbox-checked .ant-checkbox-inner {
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-select-selection {
  border-color: #2c3e50 !important;
  border-color: var(--secondary-color-2) !important;
  background-color: #2c3e50 !important;
  background-color: var(--secondary-color-2) !important;
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-select-selection:hover,
.ant-select-focused .ant-select-selection,
.ant-select-selection:focus,
.ant-select-selection:active {
  box-shadow: none !important;
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
  border-width: 2px !important;
  border-right-width: 2px !important;
}

.ant-select-dropdown-menu-item-selected,
.ant-select-dropdown-menu-item-selected:hover {
  background-color: #212b36 !important;
  background-color: var(--secondary-color-3) !important;
}

.ant-table-thead > tr > th {
  background: rgba(0, 0, 0, 0.15) !important;
}

.ant-table-thead > tr.ant-table-row-hover > td,
.ant-table-tbody > tr.ant-table-row-hover > td,
.ant-table-thead > tr:hover > td,
.ant-table-tbody > tr:hover > td,
.ant-table-tbody > tr.ant-table-row-selected td {
  background: rgba(0, 0, 0, 0.15) !important;
}

.ant-table-placeholder {
  background-color: #34495e !important;
  background-color: var(--secondary-color-1) !important;
}

.ant-radio-group-solid
  .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled),
.ant-radio-group-solid
  .ant-radio-button-wrapper-checked:not(.ant-radio-button-wrapper-disabled):hover {
  background-color: #2980b9 !important;
  background-color: var(--primary) !important;
  border-color: #2980b9 !important;
  border-color: var(--primary) !important;
}

.ant-radio-button-wrapper,
.ant-radio-button-wrapper-focused,
.ant-radio-button-wrapper:hover,
.ant-radio-button-wrapper-focused {
  color: #2980b9 !important;
  color: var(--primary) !important;
}

.ant-popover-title {
  color: #8b99a7 !important;
}

.ant-select-dropdown,
.ant-select-dropdown-menu-item-active {
  background: #2c3e50 !important;
  background: var(--secondary-color-2) !important;
}

.ant-select-dropdown-menu-item:hover {
  background: #212b36 !important;
  background: var(--secondary-color-3) !important;
}

.ant-popover-inner {
  background: #34495e !important;
  background: var(--secondary-color-1) !important;
  border: 3px solid #2980b9 !important;
  border: 3px solid var(--primary) !important;
}

.ant-popover-inner-content {
  color: white !important;
}

.ant-message-notice-content {
  background: #2c3e50 !important;
  background: var(--secondary-color-2) !important;
}

/* Add a black background color to the top navigation */
.topnav {
  background-color: var(--secondary-color-2);
  overflow: hidden;
}

.topnav i {
  padding-right: 5px;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  display: block;
  color: #c2c2c2;
  font-family: "GlacialIndifferenceRegular", sans-serif;
  font-weight: 700;
  text-align: center;
  padding: 0 16px;
  text-decoration: none;
  font-size: 17px;
  height: 45px;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  line-height: 45px;
}

/* Change the color of links on hover */
.topnav a:hover {
  border-radius: 4px;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  background-color: var(--secondary-color-3);
  color: white;
}

/* Add an active class to highlight the current page */
.active {
  border-radius: 4px;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  background-color: var(--primary) !important;
  color: white !important;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
  display: none;
}

.logoContainer {
  float: left;
  color: white;
  font-size: 25px;
  font-family: "GlacialIndifferenceRegular", sans-serif;
  padding-right: 100px;
  line-height: 45px;
  display: inline-flex;
  align-items: center;
}

/* When the screen is less than 600 pixels wide, hide all links, except for the first one ("Home"). Show the link that contains should open and close the topnav (.icon) */
@media screen and (max-width: 880px) {
  .topnav a {
    display: none;
  }
  .topnav a.icon {
    float: right;
    display: block;
    width: 45px;
  }
}

/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 880px) {
  .topnav.responsive > .logoContainer {
    display: none;
  }

  .topnav.responsive {
    position: relative;
  }
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}

