.download-header {
  width: 100%;
  background: var(--secondary-color-2);
  height: auto;
  padding: 40px 30px;
}

.download-buttons {
    display: flex;
    justify-content: center;
    margin-top: 30px;
}

.download-buttons button {
    margin: 0 15px;
    width: 120px;
    height: 50px;
}

.download-buttons button i {
    margin-right: 5px;
}